import React, {useState}  from 'react';
import { quizData } from './quizData'
import ParserCondition from './ParserCondition';

const playDOM = document.getElementById("play")
const pauseDOM = document.getElementById("pause")
const audioDOM = document.getElementById("my-audio")
export default function Decision(props) {
    const [decision, setDecision] = useState()
    const [ view, setView ] = useState(false)
    const [hideExtras, setHideExtras] = useState(false)

    console.log('quiz obj',props.quizObj)
    console.log("q in store", props.qInStore)
    console.log("my condition", ((props.quizObj && props.quizObj.userId !== props.quizObj.correctId) 
    || (props.qInStore && !props.qInStore.shouldBeDisabled)) )
    
    const handleClose = () => {
        setDecision(false);
        setHideExtras(() => false)
        props.setTempLockDown(() => false)
    }
    const handleShow = () => {
         setDecision(true)
         props.disable(true)
         setHideExtras(() => true)
         props.setTempLockDown(() => true)
         setDisabled(true)
         props.setCountForAnswer()
         props.sendQuestionToStore();
         console.log("clicked")
    }

    const openView = () => {
        props.removeVideo()
        setView(true)
        props.show()
        playDOM && (playDOM.style.display = "none");
        audioDOM && audioDOM.play()
        pauseDOM && (pauseDOM.style.display = "block");
    }


    const closeView = () => {
      
        props.changeQuestion()
        if(props.isLast){
            props.setHistory({path: "thanks"})
        }
        else{
            props.addVideo()
            setDecision(false);
            setView(false)
            setHideExtras(() => false)
             props.setTempLockDown(() => false)
            audioDOM && audioDOM.pause()
            //props.disable(false)
            setDisabled(false)
            //props.setHistory({path: "quiz", id: props.id, questionId: props.questionId + 1})
            props.finalizeTracker(props.questionId + 1)
            props.close()
        }
        
    }
    
  
    const [ disabled, setDisabled ]= useState(false)
    
    return (
      <React.Fragment>
          <section className="decision-style" >
              <div className={decision || (props.qInStore && props.qInStore.shouldBeDisabled) ? "decision": "decision-none"}>
                 <div className={view ? "view d-inline" : "viewnone"}>
                    <div className="card bg-overlay">
                        <div className="card-body scroll-y fixed-h-explain">
                            <div className="card-text font-reduced">
                                <ParserCondition
                                question={props.explanation ? props.explanation : ""}
                                isMathjax={props.explanation ? props.explanation.includes('\\(') : false}
                                />
                            </div>
                            <button className="submit move-it" onClick={closeView}>Continue</button>
                        </div>
                        </div>
                    </div>
                  <div className=" row d-inline" id="dissapear">
                      <div className="col-md-6">
                        <div className="card-body greyish">
                              <div className="padding-up">
                            
                              <h4 className="card-title text-center card-down-now incorrect">
                                  <img className="img-logo" 
                                    src={(props.quizObj && props.quizObj.userId !== props.quizObj.correctId) 
                                        ? "images/red.png" 
                                        : "images/tick.png"}/>
                                    {(props.quizObj && props.quizObj.userId === props.quizObj.correctId) ? "Correct": "Incorrect"}
                              </h4>
                              <div className="line-padding">
                                  <div className="line-ish"></div>
                              </div>
                              <AnswerStatus correct={props.quizObj && props.quizObj.userId === props.quizObj.correctId}/>
                                
                               {((props.quizObj && props.quizObj.userId !== props.quizObj.correctId) 
                                && (props.qInStore && !props.qInStore.shouldBeDisabled))
                                    &&
                                    <div className="text-center push-down">
                                        <button onClick={handleClose} className="try-submit-grey"> Try Again </button>
                                    </div>}
                                <div className="text-center push-down">
                                    <button onClick = {openView} className="try-submit-blue"> View Explanation</button>
                                </div> 
                               
                                <div className="text-center push-down">
                                   <button onClick={closeView} className="try-submit-light"> Continue</button>
                                </div> 
        
                              </div>                    
                            </div>
                      </div>
                 
                      <div className="col-md-6">
                         <img className="character" src="images/character.png"/>  
                      </div>
                  </div>
                
              </div>
             
            
          </section>
          <div>
              {hideExtras || props.qInStore && props.qInStore.shouldBeDisabled
                ? <button onClick={handleShow} disabled="disabled" className="submit"> SUBMIT</button>
                : <button onClick={handleShow} className="submit"> SUBMIT</button>
              }
                
            </div>
      </React.Fragment>
      )
}


const AnswerStatus =({correct}) => {
    console.log('stat',correct)
    return (
        <>
            <h6 className="text-center incorrect-text">{!correct  && "Incorrect"}</h6>
            {!correct && <h6 className="text-center try-gain">Please Try Again</h6>}
        </>
    )
}