import React, {useState} from 'react'

function range(start, end) {
  if(start === end) return [start];
  return [start, ...range(start + 1, end)];
}

export default function SideMenu(props) {
  console.log(props.questions)
  const finalize = (id) => {
    props.finalizeTracker(id)
  }

  const openNav = () => {
      document.getElementById("mySidenav").style.width = "200px";
    }
    
  const closeNav = () => {
    document.getElementById("mySidenav").style.width = "0";
  }


const [ disabled ]= useState(false)


return (
    <div className="d-inline position-right">
        <span className="pointer" onClick={openNav}>
              <img className="smaller-menu" src="images/ham2.png"/>
        </span>
        
              <div id="mySidenav" className="sidenav">
                    <div className="row row-menu">
                        <div className="col-md-6 question">
                        <p className="style-question">Questions</p>
                        </div>
                        <div className="col-md-6 button-close">
                        <div className="closebtn " onClick={closeNav}>
                            <img className="close-bar" src="images/close.png"/>
                        </div>
                        </div>
                    </div>
                
              
              {range(1, props.questions).map(number => ( 
                  <a onClick={(id) => finalize(number - 1)} key={number} className="c-pointer questionTag"  id={props.questionId === number - 1 ? "active-link" : "link"}>Question {number}</a>    
              ))}
              
            <button onClick={()=> props.setHistory({path:"home"})} className="c-pointer quit" >
              <div>
                <img src="images/logout.svg" />
                <span className="quit-quiz ">Quit Quiz</span>
              </div>
            </button>
        
              
           
          </div>
    
      
    </div>
  )
}
