import React from "react";
import { setHistory } from "../../../creators/historyCreator";

const RegisterForm = props => {
  console.log("sethistory props is", props);
  return (
    <div
      className="container-fluid register-wrapper"
      style={{ paddingLeft: "0px" }}
    >
      <div className="containerr">
        <div className="row">
          <div className="col-md-7 package">
            <div className="text-center reg-text">
              <h3>Access quality content, everytime</h3>
              <h5>
                There are over 1000 video lessons across several topics and
                subjects provided for you at the speed of your internet and at
                the comfort of your home. Each content is tailored to fit the
                needs of your child, ward, friend, benefactor or children you
                support in the orphanage.
              </h5>
            </div>
            <div className="text-center">
              <img
                src="images/reg-animation.svg"
                className="img-responsive text-center"
                alt="register animate"
                style={{ top: "120px", position: "relative" }}
              />
            </div>
          </div>

          <div className="col-md-5 register-details">
            <div className="text-center">
              <img
                src="images/logo-svg.svg"
                alt="studymate logo"
                style={{ width: "35%" }}
              />
              <h3>Your Dependable Learning Buddy</h3>
            </div>
            <form onSubmit={props.submitForm}>
              <div className="form-row">
                <div className="form-group col-6">
                  <input
                    {...props.firstNameField}
                    required
                    className="form-control"
                    placeholder="First Name"
                  />
                </div>
                <div className="form-group col-6">
                  <input
                    {...props.surNameField}
                    required
                    className="form-control"
                    placeholder="Last Name"
                  />
                </div>
              </div>
              <div className="form-group">
                <input
                  {...props.emailField}
                  required
                  className="form-control"
                  placeholder="Email"
                />
              </div>

              <div className="form-row">
                <div className="form-group col-4">
                  <select
                    value={props.sex}
                    onChange={e => props.setSex(e.target.value)}
                    className="form-control select_"
                  >
                    <option>Female</option>
                    <option>Male</option>
                  </select>
                </div>
                <div className="form-group col-8">
                  <input
                    {...props.phoneNumberField}
                    required
                    className="form-control"
                    placeholder="phone"
                  />
                </div>
              </div>

              <div className="agreement mt-4 mb-4 text-center">
                <span style={{ fontSize: 11 }}>
                  By submitting this form, I agree to StudyMate Privacy Policy
                  and User Agreement.
                </span>
              </div>
              <button type="submit" className="register">
                Sign in
              </button>
            </form>
            <div className="agreement mt-4 mb-4 text-center">
              {props.users && props.users.length > 0 && (
                <>
                  <span style={{ fontSize: 11 }}>Have an account?</span>
                  <button
                    className="reg-login"
                    onClick={() => props.gotoLogin()}
                  >
                    LOGIN
                  </button>
                </>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default RegisterForm;
