import React from 'react';
import caret from '../../img/caret.svg';

const TogglableItem = ({src, name}) => {
    return (
        <div className="link">
          <img src={src} alt="play button" />
          <span>{name}</span>
          <img className="caret" src={caret} alt="dropdown display" />
        </div>
    )
}

export default TogglableItem;