import React from 'react'
import NoTimeNav from '../ToRightOfNav/NoTimeNav/NoTimeNav'
import ConnectedTimeNav from '../ToRightOfNav/TimeNav/ConnectedTimeNav'
import ConnectedSearchBar from '../ToRightOfNav/Searchbar/ConnectedSearchBar'

export default function StudyNavRight({type, history, questionId}) {
    return (
        <React.Fragment>
            {type === "SEARCH_BAR" && history !== "home" && <ConnectedSearchBar />}
            {type === "LEARN_MODE" && history !== "thanks" && <NoTimeNav text="Learning Approach" questionNo={Number(questionId)+1}/>}
            {(type === "TIME_MODE" || type === "SKILL_MODE") && history !== "thanks" && <ConnectedTimeNav questionNo={Number(questionId)+1}/>}
            {type === "FREE_FORM" && history !== "thanks" && <NoTimeNav text="Free form mode" questionNo={Number(questionId)+1}/>}
            {type === "REVIEW_MODE" && history !== "thanks" && <NoTimeNav text="Review mode" questionNo={Number(questionId)+1}/>}
        </React.Fragment>
    )

}










    