                        "question": "The relationship between the tiers of government in Nigeria can be described as one of",
                        "isQuestionMathJax": false,
                        "answerId": 308,
                        "hasVideo": false,
                        "videoUrl": null,
                        "audioUrl": null,
                        "answerUrl": "Nigeria operates a federal system of government with three levels or tiers of government. These are the federal, state and local government. Both the state and local government are regarded as independent subordinate units. The federal government however, has certain constitutional powers which places it above the subordinate units.\n\nQ28.",
