                        "hasPassage": false,
                        "isFirstPassage": false,
                        "passage": null,
                        "question": "The principle of separation of power was made popular by",
                        "isQuestionMathJax": false,
                        "answerId": 320,
                        "hasVideo": false,
                        "videoUrl": null,
                        "audioUrl": null,
                        "answerUrl": "It was a French political thinker and jurist Baron de Montesquieu who developed and popularized the principle of separation of powers in his book entitled “Espirit des lois” which means the spirit of laws published in 1748. Separation of power means the division of governmental political powers that exists in any given nation into the three organs of government i.e. the executive, legislature and the judiciary.\n\nQ31.",
                        "testId": 2,
