                        "section": null,
                        "hasPassage": false,
                        "isFirstPassage": false,
                        "passage": null,
                        "question": "The law of libel limits a citizen’s right to freedom of",
                        "isQuestionMathJax": false,
                        "answerId": 330,
                        "hasVideo": false,
                        "videoUrl": null,
                        "audioUrl": null,
                        "answerUrl": "Libel itself means the act of printing a statement about somebody that is not true and that gives people a bad opinion about the person. There is a law against this act known as Law of Libel. This law is one of the restrictions on the freedom of Expression.\n\nQ34.",
