                        "section": null,
                        "hasPassage": false,
                        "isFirstPassage": false,
                        "passage": null,
                        "question": "Delegated legislation refers to the laws made by",
                        "isQuestionMathJax": false,
                        "answerId": 339,
                        "hasVideo": false,
                        "videoUrl": null,
                        "audioUrl": null,
                        "answerUrl": "To facilitate law making process, the legislature delegates part of its law-making functions to individuals, ministries, corporation, local government, etc. The law, rules and regulations made by these extra - parliamentary bodies are what we know as Delegated Legislation.\n\nQ36.",
