                        "isSectioned": false,
                        "isFirstSection": false,
                        "section": null,
                        "hasPassage": false,
                        "isFirstPassage": false,
                        "passage": null,
                        "question": "The creation of classless society is the ultimate aim of",
                        "isQuestionMathJax": false,
                        "answerId": 385,
                        "hasVideo": false,
                        "videoUrl": null,
                        "audioUrl": null,
                        "answerUrl": "Karl Max defines Communism as the last stage of socialism. Communism is a type of economic system and ideology which presupposes that there will be a time the state will wither away and there will be no government. Under communism, everybody is equal and happy.\n\nQ48.",
                        "testId": 2,
