                        "isSectioned": false,
                        "isFirstSection": false,
                        "section": null,
                        "hasPassage": false,
                        "isFirstPassage": false,
                        "passage": null,
                        "question": "The agent through which the state undertakes political socialization is the",
                        "isQuestionMathJax": false,
                        "answerId": 390,
                        "hasVideo": false,
                        "videoUrl": null,
                        "audioUrl": null,
                        "answerUrl": "In Schools, curriculum or syllabus are formulated to teach students their roles as citizens, the constitution of their country and about morality. The classroom ritual life involves things like saluting the flags, honouring past heroes, obedience to authority, etc.\n\nQ49.",
                        "testId": 2,
