                        "isSectioned": false,
                        "isFirstSection": true,
                        "section": "Passage IV (21 – 25)",
                        "hasPassage": false,
                        "isFirstPassage": false,
                        "passage": "The 2002 World Cup Competition, also called Korea/Japan 2002, kicked off with a match between the defending champions, France, and the Senegalese national team from Africa. Nobody had given the Senegalese any chance against the star – studded defending champions but the 1 – 0 score line in favour of Senegal showed that African football can no longer be taken for granted.\nThis shocking defeat of France had raised Africa’s hopes of going beyond the first round of the tournament. So when the Super Eagles of Nigeria filed out against Argentina on the mourning of Sunday, June the second, 2002m many Nigerian football enthusiasts delayed attending church service to watch the match live on television. As expected, the Super Eagles put up strong resistance to the Argentinian challenge and the day would not have ended on a sombre note for Nigerians if the momentum had been sustained throughout the match.\nThe hope of going beyond the first round, though precarious, was very much alive as the Eagles were expected to defeat their next opponents, Sweden and England. But some shortcomings in the Nigerian national team needed to be rectified to brighten their chances against their next opponents.\nFirst, the defence needed to be strengthened to prevent the opponents from incessantly terrorizing the goalkeeper. Then the strikers also needed to improve on their lacklustre performance against Argentina, since every Nigerian expected them to overwhelm their next opponents in the opening rounds. Lastly, rather than gamble with unfit players, a more creative use of the reserves would be necessary to smooth the way to the next round.\nIf World Cup debutants, Senegal, could nurse the hope of playing in the knock – out stages of the tournament then the Eagles should soar instead of being intimidated by big names, for no team is invincible.",
