                        "isQuestionMathJax": false,
                        "answerId": 9251,
                        "hasVideo": false,
                        "videoUrl": null,
                        "audioUrl": null,
                        "answerUrl": "‘Replacement of injured players, stronger attackers and a rugged defence’",
                        "testId": 45,
