import React from "react";
import ConnectedWrapper from "../Wrapper/ConnectedWrapper";
import StudyNav from "../Navigation/StudyNav/StudyNav";
import ConnectedSkillAssessment from "./ConnectedSkillAssessment";

const Home = props => {
  props.type("SEARCH_BAR");
  return (
    <React.Fragment>
      {!props.user.hasTakenTest ? (
        <React.Fragment>
          <StudyNav setHistory={props.setHistory}></StudyNav>
          <ConnectedSkillAssessment setHistory={props.setHistory} />
        </React.Fragment>
      ) : (
        <ConnectedWrapper>
          <div className="default-home text-center">
            <div className="study-background">
              <img
                src="images/homeAnimation.svg"
                alt="study background"
                className="img-responsive"
              />
            </div>
          </div>
        </ConnectedWrapper>
      )}
    </React.Fragment>
  );
};

export default Home;
