import React from 'react'

const Thankful = ({setHistory}) => {
    return (
        <>
        <div className="text-center mt-i-4">
            <img src="images/thankyou.svg" className="t-q-width" alt="thank you for taking this quiz to enrich your knowledge" />
        </div>
        <div className="realligned">
          <button className="next-button d-block " onClick={() => setHistory({path: "home"})}>Done</button>     
        </div>
          </>
    )
}

export default Thankful;
