import React, { useState, useEffect } from "react";
import Decision from "./Decision";
import ls from "../../sideEffects/ourLocalStorage";
import track from "../../sideEffects/apis/courseTracker";
import FooterHome from "../../Footer/FooterHome";
import { coursesData } from "../Data/Data";

const learnModeCss = {
  top: "127px"
};

const timeModeCss = {
  top: "160px"
};
const freeFormCss = {
  top: "240px"
};

const CoursesObj = (props) => {
  console.log("test data in courses",props.testData, props.id)
  props.type("SEARCH_BAR");
  const [show, setShow] = useState(false);
  const [mode, setMode] = useState();
  const [specificCourse, setSpecificCourse] = useState();

  const handleClose = () => {
    document.getElementById("home").style.overflowY = "scroll";
    setShow(false);
  };
  const handleShow = id => {
    setSpecificCourse(() => id);
    document.getElementById("home").style.overflowY = "hidden";
    setShow(true);
  };

  let allCourses = [];
  console.log("search is", props.search, props.testData)
  if(props.search){
    allCourses = props.testData.filter(course => props.id.some(i => i === course.id ));
  }
  else{
    allCourses = props.testData && props.testData.filter(course => course.id == props.id);
  }

  const { courses } = props.user;
  console.log("courses here", courses);
  
  const coursesWithoutSkill = courses.filter(
    course => course.mode !== "SKILL_MODE"
  );
  console.log(coursesWithoutSkill);
  const otherCourses = [];
  allCourses && allCourses.forEach(c => {
      console.log(c);
      c.tests.forEach(t => {
        //console.log(t, c);
        const available = coursesWithoutSkill.find(
          cc => {
            console.log("comaparer", cc.courseId, cc.mainId, t.courseId, t.id, c.id)
            return cc.mainId === c.id && cc.courseId === t.id
          }
        );
        console.log("ava",available)
        if (available) {
          console.log("IN HERE");
          otherCourses.push({
            ...t,
            completed: `${t.quizes && t.quizes.length > 0 ? ((available.question.length / t.quizes.length) *
              100
            ).toFixed(2) : 0}`
          });
        }
      });
    });
  console.log("hello there", otherCourses);
  const correctStyle = () => {
    switch (mode) {
      case "LEARN_MODE":
        return learnModeCss;
      case "TIME_MODE":
        return timeModeCss;
      case "FREE_FORM":
        return freeFormCss;
    }
  };

  const showMode = type => {
    setMode(type);
  };

  const setGlobalMode = (id, quiz) => {
    console.log("our quiz", quiz);
    if (mode) {
      if (mode === "TIME_MODE" && !props.time) return;
      props.setMode(mode);
      ls.persistItemInLS("mode", mode, 2);
      props.setHistory({
        path: "quiz",
        mainId: id,
        id: specificCourse,
        questionId: 0
      });
      props.setCourses(quiz.quizes);
      props.setActiveCourse(track.courseBuilder(id, specificCourse, mode));
    }
  };
  
  console.log("all coursessss", allCourses);
  return (
    <React.Fragment>
      {allCourses && allCourses.length > 0 && (
        allCourses.map(selectedCourse => (
          <section id="home" key={selectedCourse.id}>
          <div className={show ? "overlay" : "overlay-none"}>
            <div className="row downish">
              <div className="col-md-12 my-auto mx-auto text-center">
                <img
                  onClick={handleClose}
                  src="images/No.png"
                  className="close my-auto mx-auto text-center button-shift"
                />
              </div>
              <div className="col-md-12">
                <div
                  className="card centered my-auto mx-auto branded"
                  style={{ width: "54rem" }}
                >
                  <h5 className="card-header no-margin-up text-center">
                    Please select an approach
                  </h5>
                  <div className="card-body blue-box box">
                    <h1 className="approach">Learning Approach</h1>
                    <label
                      onClick={() => showMode("LEARN_MODE")}
                      className="contain"
                    >
                      Learning Mode
                      <input type="radio" name="radio" />
                      <span className="checkmark"></span>
                    </label>
                    <h1 className="approach">Test Approach</h1>
                    <label
                      onClick={() => showMode("TIME_MODE")}
                      className="contain"
                    >
                      Time Mode
                      <input type="radio" name="radio" />
                      <span className="checkmark"></span>
                    </label>
                    <label
                      onClick={() => showMode("FREE_FORM")}
                      className="contain"
                    >
                      Free Form Mode
                      <input type="radio" name="radio" />
                      <span className="checkmark"></span>
                    </label>
                  </div>

                  <div className="col-md-12 centralized">
                    <button
                      className="btn-get-started scrollto blackish"
                      onClick={() =>
                        setGlobalMode(
                          selectedCourse.id,
                          selectedCourse.tests.find(
                            quiz => quiz.id == specificCourse
                          )
                        )
                      }
                    >
                      SUBMIT
                    </button>
                  </div>
                  <Decision type={mode} style={correctStyle()} />
                </div>
              </div>
            </div>
          </div>

          <header className="home">
            <h3>{selectedCourse.name} STUDY PACK</h3>
            <p>Test your skills with these questions from Examination bodies in Nigeria</p>
          </header>

          <div className="d-flex scroll-bar">
            {selectedCourse.tests.map((datum, key) => (
              <div key={key} className="col-md-3 " style={{ marginRight: 20 }}>
                <div className="card shadowy" style={{ width: 230 }}>
                  <div className="card-body box">
                    <h5 className="card-title card-down ">{datum.year}</h5>
                    <h6 className="card-subtitle mb-2 text-muted card-down ">
                      {datum.text}
                    </h6>
                    <p className="card-text card-down ">
                      {datum.shortDescription}
                    </p>
                    <div className="row covering">
                      <div className="col-md-6 middle">
                        <div className="row">
                          <p className="new-font">Question</p>
                        </div>
                        <a href="#" className="card-link card-down duration ">
                          {datum.questionNo}
                        </a>
                      </div>
                      <div className="col-md-6 leftish">
                        <div className="row">
                          <p className="new-font">Duration</p>
                        </div>
                        <a href="#" className="card-link card-down duration ">
                          {datum.duration}
                        </a>
                      </div>
                    </div>
                    <button
                      key={datum.id}
                      className="btn btn-primary new-button"
                      onClick={() => handleShow(datum.id)}
                    >
                      {" "}
                      Start Test
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>

          <header className="home">
            <h3>Taken previously</h3>
          </header>
          <div className="d-flex scroll-bar">
            {otherCourses.map((datum, key) => (
              <div key={key} className="col-md-3 " style={{ marginRight: 20 }}>
                <div className="card shadowy" style={{ width: 230 }}>
                  <div className="card-body box">
                    <h5 className="card-title card-down ">{datum.year}</h5>
                    <h6 className="card-subtitle mb-2 text-muted card-down ">
                      {datum.text}
                    </h6>
                    <p className="card-text card-down ">{datum.title}</p>
                    <div className="row covering">
                      <div className="col-md-6 middle">
                        <div className="row">
                          <p className="new-font">Question</p>
                        </div>
                        <a href="#" className="card-link card-down duration ">
                          {datum.number}
                        </a>
                      </div>
                      <div className="col-md-6 leftish">
                        <div className="row">
                          <p className="new-font">Duration</p>
                        </div>
                        <a href="#" className="card-link card-down duration ">
                          {datum.duration}
                        </a>
                      </div>
                    </div>
                    <div>
                      <p className="completion">
                        <span className="percentage">{datum.completed}%</span>
                        <span> completed</span>
                      </p>
                      <div
                        style={{
                          width: "100%",
                          backgroundColor: "#F4F4F4",
                          borderRadius: 20,
                          height: 5.96
                        }}
                      >
                        <div
                          style={{
                            width: `${datum.completed}%`,
                            backgroundColor: "#10A3FF",
                            borderRadius: 20,
                            height: 5.96
                          }}
                        ></div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>

          <FooterHome />
        </section>
        ))
      )}
    </React.Fragment>
  );
};

export default CoursesObj;
